@font-face {
	font-family: 'Montserrat';
	src: url('../fonts/monserate/Montserrat-Thin.eot');
	src: url('../fonts/monserate/Montserrat-Thin.eot?#iefix') format('embedded-opentype');
	src: url('../fonts/monserate/Montserrat-Thin.woff2') format('woff2');
	src: url('../fonts/monserate/Montserrat-Thin.woff') format('woff');
	font-weight: 100;
	font-style: normal;
	font-display: swap; }

