* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: 'Montserrat'; }

.App {
	max-width: 1920px;
	margin: auto; }
.content {
	padding: 0 5%;
	margin: auto; }

@media (max-width: 660px) {
	.content {
		padding: 0 2%; } }
