
.header {
    background: #383737;
    padding: 45px 5%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center; }
.header__title {
    text-align: right;
    font-size: 43px;
    color: white;
    text-shadow: 5px -4px 2px #ffffff52;
    font-weight: 700; }
.form {

    display: grid;
    grid-template-columns: auto auto;
    justify-content: center;
    grid-gap: 20px; }

.form__input {
    padding: 5px 20px;
    outline: none;
    border-radius: 5px;
    width: 500px;
    font-size: 18px; }

.form__button {
    background: #8c5ad3;
    padding: 10px 40px;
    color: aliceblue;
    border-radius: 15px;
    min-width: 152px;
    width: fit-content; }

.form__button:hover {
    cursor: pointer; }

@media (max-width: 1024px) {
    .header {
        padding: 24px 2%;
        grid-template-columns: 1fr;
        justify-items: flex-start;
        grid-gap: 16px; }
    .form {
        order: 2;
        grid-template-columns: auto; }

    .form__input {
        max-width: 500px;
        width: 100%; } }

@media (max-width: 660px) {
    .form__input {
        max-width: 500px;
        width: 100%; }
    .form__button {
        width: 100%;
        text-align: center; } }
